// Import breakpoints
@import '@automator/components/icon/mixin.scss';

.uap-btn {
	$button: &;

	outline: none;
	display: inline-block;
	font-weight: 600;
	text-align: center;
	white-space: nowrap;
	vertical-align: middle;
	user-select: none;
	border: 1px solid transparent;
	padding: 5px 15px;
	font-size: 14px;
	line-height: 1.5;
	border-radius: 8px;
	background-color: transparent;
	transition: color 150ms ease-in-out, background-color 150ms ease-in-out, border-color 150ms ease-in-out, box-shadow 150ms ease-in-out;
	cursor: pointer;
	box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.1);
	position: relative;
	text-decoration: none;

	&--primary {
		color: var(--uap-color-primary);
		border-color: var(--uap-color-primary);

		&:disabled,
		&:hover,
		&:focus,
		&#{ $button }--loading,
		&#{ $button }--disabled {
			color: #fff;
			background-color: var(--uap-color-primary);
		}

		&:not(#{ $button }--disabled) {
			&:active,
			&:focus {
				box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.5);
			}
		}
	}

	&--secondary {
		color: #6c757d;
		border-color: #b7b7b7;

		&:disabled,
		&:hover,
		&:focus,
		&#{ $button }--loading,
		&#{ $button }--disabled {
			color: #fff;
			border-color: #9e9e9e;
			background-color: #9e9e9e;
		}

		&:not(#{ $button }--disabled) {
			&:active,
			&:focus {
				box-shadow: 0 0 0 0.2rem rgba(108, 117, 125, 0.2);
			}
		}
	}

	&--red {
		color: #f16666;
		border-color: #ff9494;

		&:disabled,
		&:hover,
		&:focus,
		&#{ $button }--loading,
		&#{ $button }--disabled {
			color: #fff;
			border-color: #f16666;
			background-color: #f16666;
		}

		&:not(#{ $button }--disabled) {
			&:active,
			&:focus {
				box-shadow: 0 0 0 0.2rem rgba(241, 102, 102, 0.2);
			}
		}
	}

	&--transparent {
		color: #6c757d;
		border-color: transparent;
		box-shadow: none;
		padding-left: 5px;
		padding-right: 5px;

		&:disabled,
		&:hover,
		&:focus,
		&#{ $button }--loading,
		&#{ $button }--disabled {
			color: #575d63;
			border-color: transparent;
			background-color: transparent;
		}
	}

	&--disabled,
	&:disabled {
		opacity: 0.6;
		cursor: not-allowed;
	}

	&--loading {
		color: transparent !important;

		&:before {
			@include icon;
			content: var(--uap-icon-loading);

			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);

			color: #fff;
			font-size: 16px;

			animation: rotating-centered 2s linear infinite;
		}

		&:hover {
			color: transparent !important;
		}
	}

	&--small {
		padding: 2px 7px;
	}
}