.uap-field {
    position: relative;
    width: 100%;

	&-icon {
	    position: absolute;
	    z-index: 100;
	    left: 15px;
	    top: 50%;
	    transform: translateY(-50%);

	    font-size: var(--uap-font-size--body-small) !important;
	    line-height: var(--uap-line-height--body-small) !important;
	    font-weight: 600 !important;
	}

	&-text {
	    font-size: var(--uap-font-size--body-small);
	    line-height: var(--uap-line-height--body-small) !important;
	    color: var(--uap-font-color-secondary) !important;
	    
	    outline: none !important;

	    border: 1px solid var(--uap-border-color-gray) !important;
	    border-radius: var(--uap-border-radius) !important;
	    box-shadow: var(--uap-box-shadow-small) !important;

	    width: 100%;

	    padding: 10px 12px !important;

	    transition: var(--uap-transition);
	}

	&-text:focus {
		border-color: var(--uap-color-primary) !important;
		box-shadow: 0 0 0 2px rgba(7,144,232,.2) !important;
	}

	&-text::placeholder {
	    font-size: var(--uap-font-size--body-small);
	    line-height: var(--uap-line-height--body-small);
	    color: var(--uap-font-color-secondary);
	}

	&--has-icon {
		padding: 10px 12px 10px 40px !important;
	}

}