$breakpoint_small_desktop: 1366px;
$breakpoint_mobile: 768px;

:root {
	--uap-font-family: -apple-system, system-ui, blinkmacsystemfont, "Segoe UI", roboto, "Helvetica Neue", arial, sans-serif;
	--uap-border-radius: 8px;
	--uap-box-padding: 20px;
	--uap-line-height: 1.4;

	--uap-color-primary: #0790e8;
	--uap-color-green: #6bc45a;
	--uap-color-gray: #a4a4a4;

	--uap-color-placeholder-gray: #e2e2e2;

	--uap-spacing-between-elements-xsmall: 4px;
	--uap-spacing-between-elements-small: 8px;
	--uap-spacing-between-elements: 12px;

	--uap-spacing-between-sections-small: 20px;
	--uap-spacing-between-sections: 40px;

	--uap-border-color-gray: #e4e4e4;

	--uap-shadow-dropdown: 0 5px 15px 0 rgba(0, 0, 0, 0.2);
	--uap-box-shadow: 0 4px 16px 0 rgba(0, 0, 0, 0.1);
	--uap-box-shadow-small: 0 2px 5px 0 rgba(0,0,0,.1);

	--uap-transition: all 150ms ease-in-out;
	--uap-transition-slow: all 300ms ease-in-out;

	/* Typography */

	--uap-font-color: #3c3c3c;
	--uap-font-color-warning: #eb5e37;
	--uap-font-color-secondary: #676b6f;
	--uap-font-background-color: #48ad35;
	--uap-font-background-color-secondary: #e4e4e4;

	--uap-font-family--header: "Nunito", sans-serif;

	--uap-font-size--body-xxsmall: 12px;
	--uap-line-height--body-xxsmall: 18px;

	--uap-font-size--body-xsmall: 14px;
	--uap-line-height--body-xsmall: 20px;

	--uap-font-size--body-small: 15px;
	--uap-line-height--body-small: 20px;

	--uap-font-size--body-regular: 16px;
	--uap-line-height--body-regular: 24px;

	--uap-font-size--body-large: 18px;
	--uap-line-height--body-large: 28px;

	--uap-font-size--header-xsmall: 20px;
	--uap-line-height--header-xsmall: 24px;

	--uap-font-size--header-small: 24px;
	--uap-line-height--header-small: 28px;

	--uap-font-size--header-regular: 28px;
	--uap-line-height--header-regular: 32px;

	--uap-font-size--header-large: 36px;
	--uap-line-height--header-large: 40px;

	--uap-font-size--header-xlarge: 44px;
	--uap-line-height--header-xlarge: 48px;

	/* Scrollbar */

	--uap-scrollbar-width: 10px;
	--uap-scrollbar-background: rgba(0, 0, 0, 0.09);
	--uap-scrollbar-thumb-background: rgba(0, 0, 0, 0.3);
}
