@import '@automator/components/icon/mixin.scss';

@font-face {
	font-family: "Uncanny Automator Icons";
	src:
		url( ~@automator/components/icon/fonts/uncanny-automator.ttf )  format( "truetype" ),
		url( ~@automator/components/icon/fonts/uncanny-automator.woff ) format( "woff" ),
		url( ~@automator/components/icon/fonts/uncanny-automator.svg )  format( "svg" );
	font-weight: normal;
	font-style: normal;
}

